/* colors */
.about-row {
  border: 2px solid #e9b563;
}

img.about-photo {
  border-radius: 50%;
}

/*# sourceMappingURL=about.css.map */

/* colors */
/* elements height */
#side-navigation .nav-link-side {
  position: absolute;
  color: white;
  right: -3.125rem;
  transition: 0.5s;
  padding: 0.625rem;
  margin: 0;
  width: 6.25rem;
  text-decoration: none;
  font-size: 1.125rem;
  border-radius: 0.3125rem 0 0 0.3125rem;
}

#side-navigation {
  position: fixed;
  right: 0;
  top: 0;
  height: calc(100vh + 0.9375rem);
  width: 6.25rem;
  z-index: 10;
}
#side-navigation .active {
  right: 0;
  background-color: #d4e7f7;
  color: #262626;
  font-weight: bold;
}
#side-navigation .nav-link-side:hover {
  right: 0;
}

#home-navlink-side {
  top: 0.9375rem;
}

#search-navlink-side {
  top: 4.6875rem;
}

#about-navlink-side {
  top: 8.4375rem;
}
#about-navlink-side.active {
  background-color: #e9b563;
}

#gallery-navlink-side {
  top: 12.1875rem;
}

#contacts-navlink-side {
  top: 15.9375rem;
}

#login-button {
  top: 19.6875rem;
  border: none;
}

/*# sourceMappingURL=navigation.css.map */

/* colors */
/* elements height */
/* colors */
/* elements height */
.main-row-f, .main-row, .main-container, #root, html, body {
  width: 100%;
  margin: 0;
  max-width: 100vw;
}

#root, html, body {
  padding: 0;
  min-height: 100vh;
}

#root {
  font-family: "Quicksand", "Arial", sans-serif;
  font-size: 1rem;
  position: relative;
}

.main-container {
  padding: 0 5vw;
}

.main-row {
  min-height: 100vh;
  padding: 0 0 4.375rem 0;
}

.row-height {
  min-height: 6.25rem;
  height: calc((100vh - 4.375rem - 1.25rem)/ 3 - 1.25rem);
  max-height: 25rem;
  overflow: auto;
}

.row-m-p {
  margin: 0 0 0.625rem 0;
  padding: 0.3125rem;
}

.col-m-p {
  margin: 0.625rem;
  padding: 0.3125rem;
}

.p-m-p {
  margin: 0.3125rem;
  padding: 0.625rem;
}

.image-size {
  min-height: 5.625rem;
  height: calc(calc((100vh - 4.375rem - 1.25rem)/ 3 - 1.25rem) - 0.625rem);
  max-width: 100%;
  max-height: 20rem;
}

.dark-bg {
  background-color: #262626;
}

.gray-bg {
  background-color: #666666;
}

.blue-bg {
  background-color: #d4e7f7;
}

.beige-bg {
  background-color: #fce6cf;
}

.gold-bg {
  background-color: #e9b563;
}

.relative {
  position: relative;
}

.dark-link {
  color: #262626;
}

.bright-link {
  color: white;
}

.opacity-08 {
  opacity: 0.8;
}

@media (max-width: 767px) {
  .main-row {
    padding: 0 0 9.6875rem 0;
  }

  #root {
    font-size: 1rem;
  }
}
.main-row-f {
  min-height: 4.375rem;
  padding: 0;
}

#footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4.375rem;
}
#footer .nav-link-footer {
  margin: 0 0.625rem 0 0;
  padding: 0.3125rem 0.3125rem 0.3125rem 0;
  color: white;
}
#footer .nav-link-footer:hover {
  text-decoration: underline;
}

@media (max-width: 767px) {
  #footer {
    height: 9.6875rem;
  }
}

/*# sourceMappingURL=footer.css.map */

